<template>
  <div class="main">
    <van-sticky>
      <van-row class="header">
        <van-col span="3" @click="showPopup" v-if="userInfo">
          <van-icon class="user-icon" size="32" name="https://b.yzcdn.cn/vant/icon-demo-1126.png" />
        </van-col>
        <van-col :span="userInfo?18:21">
          <van-tabs @change="onTabChange" v-model="tabIndex">
            <van-tab v-for="(item,index) in tabs" :title="item.name" :key="index" />
          </van-tabs>
        </van-col>
        <van-col span="3">
          <van-icon size="30" class="search-icon" name="search" />
        </van-col>
      </van-row>
    </van-sticky>
    <div class="content">
      <van-list ref="list" error-text="请求失败，点击重新加载">
        <van-panel
          class="list-item"
          v-for="(item,index) in list"
          :key="index"
          @click="getArticlesDetail(item._id)"
        >
          <template #header>
            <div class="item-header">
              <span>{{(index+1)+"、"+item.title}}</span>
              <van-icon size="24" name="like-o" v-if="userInfo" />
            </div>
          </template>
          <div class="item-content">
            <lazy-component
              class="image"
              v-for="(imageUrl,imageIndex) in item.thumbnails"
              :key="imageIndex"
            >
              <van-image class="image" lazy-load :src="imageUrl" />
            </lazy-component>
          </div>
          <template #footer>
            <div class="item-footer">
              <span>{{item.category}}</span>
              <span>{{$formatTime(item._createTime)}}</span>
            </div>
          </template>
        </van-panel>
        <van-cell class="bottom" />
      </van-list>
    </div>
    <van-pagination
      class="footer"
      @change="onPageChange"
      v-model="currentPage"
      :total-items="totalCount"
      :show-page-size="5"
      :items-per-page="pageSize"
      force-ellipses
    />
    <van-popup
      v-model="showMenu"
      class="left-menu"
      round
      closeable
      position="left"
      :style="{ width: '70%' ,height:'100%' }"
    >
      <van-card v-if="userInfo">
        <template #thumb>
          <van-image round width="40px" height="40px" src="https://img.yzcdn.cn/vant/cat.jpeg" />
        </template>
        <template #tags>
          金币：${{userInfo.coin}}
          <van-tag plain type="danger" size="medium">充值</van-tag>
        </template>
        <template #title>
          <div class="title">
            <span>{{userInfo.email}}</span>
            <span class="desc">{{userInfo.role!= null && userInfo.role.name== "vip"?"VIP会员":"普通会员"}}</span>
            <span class="desc">{{userInfo.role!= null && userInfo.role.name== "vip"?"VIP会员":""}}</span>
          </div>
        </template>
      </van-card>
      <van-cell title="我的收藏" icon="like-o" />
      <van-cell title="已购主题" icon="completed" />
      <van-cell title="充值中心" icon="gold-coin-o" />
      <van-cell title="关于我们" icon="info-o" />
      <van-cell title="退出登录" icon="upgrade" />
    </van-popup>
    <van-overlay :show="showLoading" @click="showLoading = false">
      <div class="wrapper">
        <van-loading color="#fff" size="60" />
      </div>
    </van-overlay>
  </div>
</template>

<script>
import {
  Tabs,
  Tab,
  Row,
  Tag,
  Col,
  Icon,
  Cell,
  Popup,
  Sticky,
  List,
  Pagination,
  Panel,
  Loading,
  Image,
  Button,
  Card,
  Overlay,
  Toast
} from "vant";

export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Row.name]: Row,
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Cell.name]: Cell,
    [Icon.name]: Icon,
    [Popup.name]: Popup,
    [Loading.name]: Loading,
    [Sticky.name]: Sticky,
    [List.name]: List,
    [Panel.name]: Panel,
    [Image.name]: Image,
    [Button.name]: Button,
    [Card.name]: Card,
    [Overlay.name]: Overlay,
    [Pagination.name]: Pagination
  },

  data() {
    return {
      tabs: [],
      list: [],
      showMenu: false,
      showLoading: false,
      totalPage: 0,
      totalCount: 0,
      pageSize: 20,
      currentPage: 1,
      tabIndex: 0,
      userInfo: null,
      endTime: ""
    };
  },
  created() {
    // this.getMenus();
    this.goLogin();
  },
  methods: {
    goLogin() {
      let data = {
        email: "939320546@qq.com",
        password: "Aa205410",
        version: "1.0.1",
        device: "h5"
      };

      http.post("/user/login", data).then(res => {
        // console.log(res)
        if (res.status == 0) {
          this.tabs = res.data.menu;
          this.userInfo = res.data.userInfo;
          try {
            if (res.data.userInfo.role.endTime) {
              this.endTime = formatTime(res.data.userInfo.role.endTime);
            }
          } catch (error) {}

          this.$setToken(res.data.userInfo.token);
          this.getArticles();
        }
      });
    },
    getMenus() {
      let data = {
        version: "1.0.1",
        device: "h5"
      };

      http.post("/user/categorys", data).then(res => {
        if (res.status == 0 && res.data) {
          this.tabs = res.data;
          this.getArticles();
        }
      });
    },

    getArticles() {
      let param = {
        pageIndex: this.currentPage - 1, //页码
        pageSize: this.pageSize,
        category: this.tabs[this.tabIndex].id,
        type: 0
      };
      this.showLoading = true;
      http
        .post("/article/paging", param, {
          headers: { token: this.$getToken() }
        })
        .then(res => {
          this.showLoading = false;
          if (res.status == 0) {
            if (this.currentPage - 1 == res.data.pageIndex) {
              this.totalCount = res.data.totalCount;
              this.list = res.data.list;
            }
          }
        });
    },
    getArticlesDetail(detailId) {
      let param = {
        _id: detailId
      };
      this.showLoading = true;
      http
        .post("/article/detail", param, {
          headers: { token: this.$getToken() }
        })
        .then(res => {
          this.showLoading = false;
          if (res.status == 0) {
            if (res.data && res.data.title) {
              localStorage.setItem("article_detail", JSON.stringify(res.data));
              window._article_detail = res.data;
              this.$router.push("detail");
            }
          }
        });
    },
    onTabChange(index) {
      this.currentPage = 1;
      this.tabIndex = index;
      this.getArticles();
    },
    onPageChange() {
      this.getArticles();
    },
    showPopup() {
      if (!this.userInfo) {
        this.showMenu = false;
        return;
      }
      this.showMenu = !this.showMenu;
    }
  }
};
</script>

<style lang="less">
.header {
  background: #fff;
}
.user-icon {
  margin-top: 6px;
  margin-left: 10px;
}
.search-icon {
  margin-top: 6px;
  margin-left: 10px;
}
.content {
  .list-item {
    border-style: solid;
    border-width: 0 0 8px 0px;
    border-color: #ebedf0;
  }
  .item-header {
    display: flex;
    flex-direction: row;
    padding: 0px 16px;
    justify-content: space-between;
    align-items: center;
    border-style: solid;
    border-width: 0 0 1px 0px;
    border-color: #ebedf0;
    span {
      color: #323233;
      font-size: 18px;
      padding: 8px 0px;
    }
  }
  .item-content {
    display: flex;
    flex-direction: row;
    padding: 5px 16px 1px 16px;
    justify-content: space-between;
  }
  .item-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    span {
      color: #969799;
      font-size: 12px;
      line-height: 16px;
    }
  }
  .image {
    width: 22vw;
    height: 22vw;
  }
}
.left-menu {
  .van-card__thumb {
    width: 40px;
    height: 40px;
  }
  .title {
    display: flex;
    flex-direction: column;
    color: #323233;
    font-size: 16px;
    .desc {
      color: #646566;
      font-size: 12px;
    }
  }
}

.bottom {
  height: 40px;
}
.footer {
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: #fff;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
</style>